'use client';

import { useState, useEffect, useCallback } from 'react';
import {
    Plus,
    Filter,
    Download,
    Eye,
    Edit,
    Trash2,
    Mail,
    DollarSign,
    Search,
    FileText,
    CreditCard,
    Calendar,
    Users,
    TrendingUp,
} from 'lucide-react';
import { Invoice, Payment } from '@/lib/types';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Table, ActionCell } from '@/components/ui/Table';
import { InvoiceViewModal } from '@/components/billing/InvoiceViewModal';
import { InvoiceFormModal } from '@/components/billing/InvoiceFormModal';
import { PaymentModal } from '@/components/billing/PaymentModal';
import { formatCurrency, formatDate, getStatusColor } from '@/lib/utils';
import { api } from '@/lib/api';

export default function BillingPage() {
    // State
    const [invoices, setInvoices] = useState<Invoice[]>([]);
    const [payments, setPayments] = useState<Payment[]>([]);
    const [clients, setClients] = useState<Array<{ id: string; name: string; email: string }>>([]);
    const [stats, setStats] = useState({
        totalRevenue: 0,
        pendingAmount: 0,
        overdueAmount: 0,
        paidInvoices: 0,
        pendingInvoices: 0,
        overdueInvoices: 0,
    });

    // Modals
    const [selectedInvoice, setSelectedInvoice] = useState<Invoice | null>(null);
    const [isViewModalOpen, setIsViewModalOpen] = useState(false);
    const [isFormModalOpen, setIsFormModalOpen] = useState(false);
    const [isPaymentModalOpen, setIsPaymentModalOpen] = useState(false);
    const [isDeleteConfirmOpen, setIsDeleteConfirmOpen] = useState(false);

    // Filters
    const [searchTerm, setSearchTerm] = useState('');
    const [statusFilter, setStatusFilter] = useState<string>('all');
    const [dateRange, setDateRange] = useState({ start: '', end: '' });

    // Fetch data
    const fetchData = useCallback(async () => {
        try {
            const [invoicesData, paymentsData, clientsData, statsData] = await Promise.all([
                api.getInvoices(),
                api.getPayments(),
                api.getClients(),
                api.getUsageStats(),
            ]);

            setInvoices(invoicesData || []);
            setPayments(paymentsData || []);
            setClients(clientsData?.map(c => ({ id: c.id, name: c.name, email: c.email })) || []);
            setStats({
                totalRevenue: statsData?.revenue || 0,
                pendingAmount: 0, // Calculate from invoices
                overdueAmount: 0, // Calculate from invoices
                paidInvoices: invoicesData?.filter(i => i.status === 'paid').length || 0,
                pendingInvoices: invoicesData?.filter(i => i.status === 'sent').length || 0,
                overdueInvoices: invoicesData?.filter(i => i.status === 'overdue').length || 0,
            });
        } catch (error) {
            console.error('Error fetching billing data:', error);
        }
    }, []);

    useEffect(() => {
        fetchData();
    }, [fetchData]);

    // Filter invoices
    const filteredInvoices = invoices.filter(invoice => {
        const matchesSearch = searchTerm === '' ||
            invoice.clientName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            invoice.invoiceNumber.toLowerCase().includes(searchTerm.toLowerCase());

        const matchesStatus = statusFilter === 'all' || invoice.status === statusFilter;

        const matchesDate = (!dateRange.start || invoice.date >= dateRange.start) &&
            (!dateRange.end || invoice.date <= dateRange.end);

        return matchesSearch && matchesStatus && matchesDate;
    });

    // Handlers
    const handleViewInvoice = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        setIsViewModalOpen(true);
    };

    const handleEditInvoice = (invoice: Invoice) => {
        setSelectedInvoice(invoice);
        setIsFormModalOpen(true);
    };

    const handleDeleteInvoice = async () => {
        if (!selectedInvoice) return;

        try {
            await api.deleteInvoice(selectedInvoice.id);
            await fetchData();
            setIsDeleteConfirmOpen(false);
            setSelectedInvoice(null);
        } catch (error) {
            console.error('Error deleting invoice:', error);
        }
    };

    const handleCreateInvoice = () => {
        setSelectedInvoice(null);
        setIsFormModalOpen(true);
    };

    const handleSaveInvoice = async (formData: any) => {
        try {
            if (selectedInvoice) {
                await api.updateInvoice(selectedInvoice.id, formData);
            } else {
                await api.createInvoice(formData);
            }
            await fetchData();
        } catch (error) {
            console.error('Error saving invoice:', error);
        }
    };

    const handleSendInvoice = async () => {
        if (!selectedInvoice) return;

        try {
            await api.sendInvoice(selectedInvoice.id);
            await fetchData();
            setIsViewModalOpen(false);
        } catch (error) {
            console.error('Error sending invoice:', error);
        }
    };

    const handleRecordPayment = async (paymentData: any) => {
        if (!selectedInvoice) return;

        try {
            await api.markInvoiceAsPaid(selectedInvoice.id, paymentData);
            await fetchData();
            setIsViewModalOpen(false);
        } catch (error) {
            console.error('Error recording payment:', error);
        }
    };

    const handleExport = () => {
        // Export logic here
        const csv = [
            ['Invoice #', 'Client', 'Date', 'Due Date', 'Status', 'Amount'].join(','),
            ...filteredInvoices.map(inv => [
                inv.invoiceNumber,
                inv.clientName,
                inv.date,
                inv.dueDate,
                inv.status,
                inv.total,
            ].join(',')),
        ].join('\n');

        const blob = new Blob([csv], { type: 'text/csv' });
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement('a');
        a.href = url;
        a.download = `invoices-${new Date().toISOString().split('T')[0]}.csv`;
        a.click();
    };

    // Table columns
    const columns = [
        {
            key: 'invoiceNumber',
            header: 'Invoice #',
            render: (invoice: Invoice) => (
                <div>
                    <div className="font-medium text-gray-900">{invoice.invoiceNumber}</div>
                    <div className="text-sm text-gray-500">{invoice.clientName}</div>
                </div>
            ),
        },
        {
            key: 'date',
            header: 'Date',
            render: (invoice: Invoice) => (
                <div>
                    <div>{formatDate(invoice.date)}</div>
                    <div className="text-sm text-gray-500">Due: {formatDate(invoice.dueDate)}</div>
                </div>
            ),
        },
        {
            key: 'amount',
            header: 'Amount',
            render: (invoice: Invoice) => (
                <div className="font-bold text-gray-900">{formatCurrency(invoice.total)}</div>
            ),
        },
        {
            key: 'status',
            header: 'Status',
            render: (invoice: Invoice) => (
                <span className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(invoice.status)}`}>
                    {invoice.status.charAt(0).toUpperCase() + invoice.status.slice(1)}
                </span>
            ),
        },
        {
            key: 'actions',
            header: 'Actions',
            render: (invoice: Invoice) => (
                <ActionCell
                    actions={[
                        {
                            label: 'View',
                            icon: <Eye className="h-4 w-4" />,
                            onClick: () => handleViewInvoice(invoice),
                        },
                        {
                            label: 'Edit',
                            icon: <Edit className="h-4 w-4" />,
                            onClick: () => handleEditInvoice(invoice),
                            variant: invoice.status === 'paid' ? 'default' : 'default',
                        },
                        {
                            label: invoice.status === 'sent' ? 'Record Payment' : 'Send',
                            icon: invoice.status === 'sent' ? <DollarSign className="h-4 w-4" /> : <Mail className="h-4 w-4" />,
                            onClick: () => {
                                setSelectedInvoice(invoice);
                                if (invoice.status === 'sent') {
                                    setIsPaymentModalOpen(true);
                                } else {
                                    handleSendInvoice();
                                }
                            },
                        },
                        {
                            label: 'Delete',
                            icon: <Trash2 className="h-4 w-4" />,
                            onClick: () => {
                                setSelectedInvoice(invoice);
                                setIsDeleteConfirmOpen(true);
                            },
                            variant: 'danger',
                        },
                    ]}
                />
            ),
        },
    ];

    return (
        <div className="space-y-6 animate-fade-in">
            {/* Header */}
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900">Billing</h1>
                    <p className="text-gray-600 mt-2">Manage invoices, payments, and billing cycles</p>
                </div>
                <div className="flex space-x-3">
                    <Button
                        variant="outline"
                        onClick={handleExport}
                        icon={<Download className="h-4 w-4" />}
                    >
                        Export
                    </Button>
                    <Button
                        variant="primary"
                        onClick={handleCreateInvoice}
                        icon={<Plus className="h-4 w-4" />}
                    >
                        New Invoice
                    </Button>
                </div>
            </div>

            {/* Stats Cards */}
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-green-100 rounded-xl">
                            <DollarSign className="h-6 w-6 text-green-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Total Revenue</p>
                            <p className="text-2xl font-bold mt-1">{formatCurrency(stats.totalRevenue)}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-blue-100 rounded-xl">
                            <FileText className="h-6 w-6 text-blue-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Paid Invoices</p>
                            <p className="text-2xl font-bold mt-1">{stats.paidInvoices}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-amber-100 rounded-xl">
                            <CreditCard className="h-6 w-6 text-amber-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Pending</p>
                            <p className="text-2xl font-bold mt-1">{stats.pendingInvoices}</p>
                        </div>
                    </div>
                </Card>

                <Card className="p-5">
                    <div className="flex items-center">
                        <div className="p-3 bg-red-100 rounded-xl">
                            <Calendar className="h-6 w-6 text-red-600" />
                        </div>
                        <div className="ml-4">
                            <p className="text-sm font-medium text-gray-600">Overdue</p>
                            <p className="text-2xl font-bold mt-1">{stats.overdueInvoices}</p>
                        </div>
                    </div>
                </Card>
            </div>

            {/* Filters */}
            <Card>
                <div className="flex flex-col md:flex-row md:items-center gap-4 p-4">
                    <div className="flex-1">
                        <div className="relative">
                            <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                            <input
                                type="text"
                                placeholder="Search invoices by client or invoice #"
                                className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                            />
                        </div>
                    </div>

                    <div className="flex flex-wrap gap-3">
                        <select
                            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                            value={statusFilter}
                            onChange={(e) => setStatusFilter(e.target.value)}
                        >
                            <option value="all">All Status</option>
                            <option value="draft">Draft</option>
                            <option value="sent">Sent</option>
                            <option value="paid">Paid</option>
                            <option value="overdue">Overdue</option>
                        </select>

                        <input
                            type="date"
                            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                            value={dateRange.start}
                            onChange={(e) => setDateRange(prev => ({ ...prev, start: e.target.value }))}
                            placeholder="From"
                        />

                        <input
                            type="date"
                            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                            value={dateRange.end}
                            onChange={(e) => setDateRange(prev => ({ ...prev, end: e.target.value }))}
                            placeholder="To"
                        />

                        <Button
                            variant="outline"
                            onClick={() => {
                                setSearchTerm('');
                                setStatusFilter('all');
                                setDateRange({ start: '', end: '' });
                            }}
                            icon={<Filter className="h-4 w-4" />}
                        >
                            Clear Filters
                        </Button>
                    </div>
                </div>
            </Card>

            {/* Invoices Table */}
            <div className="overflow-hidden rounded-2xl shadow-ultra border border-gray-100">
                <Table
                    data={filteredInvoices}
                    columns={columns}
                    onRowClick={handleViewInvoice}
                    emptyMessage="No invoices found. Create your first invoice!"
                />

                {/* Table Footer */}
                <div className="flex items-center justify-between border-t border-gray-200 px-6 py-4 bg-white">
                    <div className="text-sm text-gray-500">
                        Showing {filteredInvoices.length} of {invoices.length} invoices
                    </div>
                    <div className="flex space-x-2">
                        <Button
                            variant="outline"
                            size="sm"
                            disabled={true}
                        >
                            Previous
                        </Button>
                        <Button
                            variant="primary"
                            size="sm"
                        >
                            1
                        </Button>
                        <Button
                            variant="outline"
                            size="sm"
                        >
                            Next
                        </Button>
                    </div>
                </div>
            </div>

            {/* Recent Payments */}
            <Card>
                <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-semibold text-gray-900">Recent Payments</h2>
                    <Button
                        variant="ghost"
                        size="sm"
                        onClick={() => {/* Navigate to payments page */ }}
                    >
                        View All
                    </Button>
                </div>

                <div className="space-y-4">
                    {payments.slice(0, 5).map(payment => (
                        <div key={payment.id} className="flex items-center justify-between p-3 hover:bg-gray-50 rounded-xl transition-colors">
                            <div className="flex items-center space-x-4">
                                <div className="p-2 bg-green-100 rounded-lg">
                                    <DollarSign className="h-5 w-5 text-green-600" />
                                </div>
                                <div>
                                    <p className="font-medium text-gray-900">{payment.clientName}</p>
                                    <p className="text-sm text-gray-500">
                                        {payment.invoiceNumber}  {payment.method.replace('_', ' ').toUpperCase()}
                                    </p>
                                </div>
                            </div>
                            <div className="text-right">
                                <p className="font-bold text-gray-900">{formatCurrency(payment.amount)}</p>
                                <p className="text-sm text-gray-500">{formatDate(payment.date)}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </Card>

            {/* Modals */}
            {selectedInvoice && (
                <>
                    <InvoiceViewModal
                        invoice={selectedInvoice}
                        isOpen={isViewModalOpen}
                        onClose={() => setIsViewModalOpen(false)}
                        onEdit={() => {
                            setIsViewModalOpen(false);
                            setIsFormModalOpen(true);
                        }}
                        onDelete={() => {
                            setIsViewModalOpen(false);
                            setIsDeleteConfirmOpen(true);
                        }}
                        onSend={handleSendInvoice}
                        onMarkAsPaid={() => {
                            setIsViewModalOpen(false);
                            setIsPaymentModalOpen(true);
                        }}
                    />

                    <PaymentModal
                        invoice={selectedInvoice}
                        isOpen={isPaymentModalOpen}
                        onClose={() => setIsPaymentModalOpen(false)}
                        onSubmit={handleRecordPayment}
                    />
                </>
            )}

            <InvoiceFormModal
                isOpen={isFormModalOpen}
                onClose={() => setIsFormModalOpen(false)}
                onSubmit={handleSaveInvoice}
                initialData={selectedInvoice}
                clients={clients}
            />

            {/* Delete Confirmation Modal */}
            {isDeleteConfirmOpen && (
                <div className="fixed inset-0 z-50 overflow-y-auto">
                    <div className="flex min-h-screen items-center justify-center p-4">
                        <div
                            className="fixed inset-0 bg-black/50 backdrop-blur-sm"
                            onClick={() => setIsDeleteConfirmOpen(false)}
                        />

                        <div className="relative z-50 w-full max-w-md bg-white rounded-2xl shadow-2xl p-6">
                            <div className="text-center">
                                <div className="mx-auto flex h-12 w-12 items-center justify-center rounded-full bg-red-100">
                                    <Trash2 className="h-6 w-6 text-red-600" />
                                </div>

                                <h3 className="mt-4 text-lg font-bold text-gray-900">Delete Invoice</h3>
                                <p className="mt-2 text-gray-600">
                                    Are you sure you want to delete invoice #{selectedInvoice?.invoiceNumber}?
                                    This action cannot be undone.
                                </p>

                                <div className="mt-6 flex justify-center space-x-3">
                                    <Button
                                        variant="outline"
                                        onClick={() => setIsDeleteConfirmOpen(false)}
                                    >
                                        Cancel
                                    </Button>

                                    <Button
                                        variant="danger"
                                        onClick={handleDeleteInvoice}
                                    >
                                        Delete Invoice
                                    </Button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}