// src/components/ui/Table.tsx
import { ReactNode } from 'react';
import { cn } from '@/lib/utils';

interface Column<T> {
    key: keyof T | string;
    header: string;
    render?: (item: T) => ReactNode;
    className?: string;
}

interface TableProps<T> {
    data: T[];
    columns: Column<T>[];
    className?: string;
    onRowClick?: (item: T) => void;
    emptyMessage?: string;
}

export function Table<T extends { id: string }>({
    data,
    columns,
    className,
    onRowClick,
    emptyMessage = 'No data available',
}: TableProps<T>) {
    return (
        <div className={cn('overflow-x-auto bg-white rounded-2xl shadow-ultra border border-gray-100', className)}>
            <table className="w-full">
                <thead>
                    <tr className="border-b border-gray-200">
                        {columns.map((column) => (
                            <th
                                key={String(column.key)}
                                className={cn(
                                    'text-left py-4 px-6 font-semibold text-gray-900 text-sm',
                                    column.className
                                )}
                            >
                                {column.header}
                            </th>
                        ))}
                    </tr>
                </thead>
                <tbody className="divide-y divide-gray-100">
                    {data.length > 0 ? (
                        data.map((item) => (
                            <tr
                                key={item.id}
                                onClick={() => onRowClick?.(item)}
                                className={cn(
                                    'hover:bg-gray-50/50 transition-colors',
                                    onRowClick && 'cursor-pointer'
                                )}
                            >
                                {columns.map((column) => (
                                    <td key={String(column.key)} className="py-4 px-6">
                                        {column.render
                                            ? column.render(item)
                                            : String(item[column.key as keyof T] || '')}
                                    </td>
                                ))}
                            </tr>
                        ))
                    ) : (
                        <tr>
                            <td
                                colSpan={columns.length}
                                className="py-12 text-center text-gray-500"
                            >
                                {emptyMessage}
                            </td>
                        </tr>
                    )}
                </tbody>
            </table>
        </div>
    );
}

// Action Cell Component for tables
interface ActionCellProps {
    actions: {
        label: string;
        icon: ReactNode;
        onClick: () => void;
        variant?: 'default' | 'danger';
    }[];
}

export function ActionCell({ actions }: ActionCellProps) {
    return (
        <div className="flex space-x-2">
            {actions.map((action, index) => (
                <button
                    key={index}
                    onClick={(e) => {
                        e.stopPropagation();
                        action.onClick();
                    }}
                    className={cn(
                        'p-2 rounded-lg transition-colors',
                        action.variant === 'danger'
                            ? 'text-red-600 hover:text-red-700 hover:bg-red-50'
                            : 'text-gray-600 hover:text-primary hover:bg-primary/5'
                    )}
                    title={action.label}
                >
                    {action.icon}
                </button>
            ))}
        </div>
    );
}