'use client';

import { useState, useEffect } from 'react';
import { X, Plus, Trash2 } from 'lucide-react';
import { Invoice, BillingItem, BillingFormData } from '@/lib/types';
import { Button } from '@/components/ui/Button';
import { formatCurrency } from '@/lib/utils';

interface InvoiceFormModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSubmit: (data: BillingFormData) => void;
    initialData?: Invoice | null;
    clients: Array<{ id: string; name: string; email: string }>;
}

export function InvoiceFormModal({
    isOpen,
    onClose,
    onSubmit,
    initialData,
    clients,
}: InvoiceFormModalProps) {
    const [formData, setFormData] = useState<BillingFormData>({
        clientId: '',
        clientName: '',
        clientEmail: '',
        invoiceNumber: '',
        date: new Date().toISOString().split('T')[0],
        dueDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
        items: [{ id: '1', description: '', quantity: 1, unitPrice: 0, total: 0 }],
        notes: '',
        tax: 0,
        discount: 0,
    });

    const [calculations, setCalculations] = useState({
        subtotal: 0,
        total: 0,
    });

    useEffect(() => {
        if (initialData) {
            setFormData({
                clientId: initialData.clientId,
                clientName: initialData.clientName,
                clientEmail: initialData.clientEmail,
                invoiceNumber: initialData.invoiceNumber,
                date: initialData.date,
                dueDate: initialData.dueDate,
                items: initialData.items,
                notes: initialData.notes || '',
                tax: initialData.tax,
                discount: initialData.discount,
            });
        } else {
            // Generate invoice number
            const year = new Date().getFullYear();
            const month = (new Date().getMonth() + 1).toString().padStart(2, '0');
            const random = Math.floor(Math.random() * 1000).toString().padStart(3, '0');
            setFormData(prev => ({
                ...prev,
                invoiceNumber: `INV-${year}${month}-${random}`,
            }));
        }
    }, [initialData]);

    useEffect(() => {
        const subtotal = formData.items.reduce((sum, item) => sum + (item.quantity * item.unitPrice), 0);
        const total = subtotal + formData.tax - formData.discount;
        setCalculations({ subtotal, total });
    }, [formData.items, formData.tax, formData.discount]);

    if (!isOpen) return null;

    const handleClientChange = (clientId: string) => {
        const client = clients.find(c => c.id === clientId);
        if (client) {
            setFormData(prev => ({
                ...prev,
                clientId,
                clientName: client.name,
                clientEmail: client.email,
            }));
        }
    };

    const handleItemChange = (index: number, field: keyof BillingItem, value: any) => {
        const newItems = [...formData.items];
        const item = { ...newItems[index] };

        if (field === 'description') {
            item.description = value;
        } else if (field === 'quantity') {
            item.quantity = parseInt(value) || 0;
        } else if (field === 'unitPrice') {
            item.unitPrice = parseFloat(value) || 0;
        }

        item.total = item.quantity * item.unitPrice;
        newItems[index] = item;

        setFormData(prev => ({ ...prev, items: newItems }));
    };

    const addItem = () => {
        setFormData(prev => ({
            ...prev,
            items: [
                ...prev.items,
                { id: Date.now().toString(), description: '', quantity: 1, unitPrice: 0, total: 0 },
            ],
        }));
    };

    const removeItem = (index: number) => {
        if (formData.items.length > 1) {
            const newItems = formData.items.filter((_, i) => i !== index);
            setFormData(prev => ({ ...prev, items: newItems }));
        }
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        onSubmit(formData);
        onClose();
    };

    return (
        <div className="fixed inset-0 z-50 overflow-y-auto">
            <div className="flex min-h-screen items-center justify-center p-4">
                <div
                    className="fixed inset-0 bg-black/50 backdrop-blur-sm"
                    onClick={onClose}
                />

                <div className="relative z-50 w-full max-w-4xl bg-white rounded-2xl shadow-2xl">
                    <div className="flex items-center justify-between border-b border-gray-200 px-6 py-4">
                        <h2 className="text-xl font-bold text-gray-900">
                            {initialData ? 'Edit Invoice' : 'Create New Invoice'}
                        </h2>
                        <button
                            onClick={onClose}
                            className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg"
                        >
                            <X className="h-5 w-5" />
                        </button>
                    </div>

                    <form onSubmit={handleSubmit} className="p-6">
                        <div className="space-y-6">
                            {/* Client Selection */}
                            <div className="grid grid-cols-2 gap-6">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Client *
                                    </label>
                                    <select
                                        value={formData.clientId}
                                        onChange={(e) => handleClientChange(e.target.value)}
                                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                        required
                                    >
                                        <option value="">Select a client</option>
                                        {clients.map(client => (
                                            <option key={client.id} value={client.id}>
                                                {client.name} ({client.email})
                                            </option>
                                        ))}
                                    </select>
                                </div>

                                <div className="grid grid-cols-2 gap-4">
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Invoice Date *
                                        </label>
                                        <input
                                            type="date"
                                            value={formData.date}
                                            onChange={(e) => setFormData(prev => ({ ...prev, date: e.target.value }))}
                                            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                            required
                                        />
                                    </div>

                                    <div>
                                        <label className="block text-sm font-medium text-gray-700 mb-2">
                                            Due Date *
                                        </label>
                                        <input
                                            type="date"
                                            value={formData.dueDate}
                                            onChange={(e) => setFormData(prev => ({ ...prev, dueDate: e.target.value }))}
                                            className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                            required
                                        />
                                    </div>
                                </div>
                            </div>

                            {/* Invoice Items */}
                            <div>
                                <div className="flex items-center justify-between mb-4">
                                    <h3 className="text-lg font-medium text-gray-900">Items</h3>
                                    <Button
                                        type="button"
                                        variant="outline"
                                        size="sm"
                                        onClick={addItem}
                                        icon={<Plus className="h-4 w-4" />}
                                    >
                                        Add Item
                                    </Button>
                                </div>

                                <div className="space-y-4">
                                    {formData.items.map((item, index) => (
                                        <div key={item.id} className="grid grid-cols-12 gap-4 items-center">
                                            <div className="col-span-5">
                                                <input
                                                    type="text"
                                                    placeholder="Description"
                                                    value={item.description}
                                                    onChange={(e) => handleItemChange(index, 'description', e.target.value)}
                                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                                    required
                                                />
                                            </div>

                                            <div className="col-span-2">
                                                <input
                                                    type="number"
                                                    placeholder="Qty"
                                                    value={item.quantity}
                                                    onChange={(e) => handleItemChange(index, 'quantity', e.target.value)}
                                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                                    min="1"
                                                    required
                                                />
                                            </div>

                                            <div className="col-span-3">
                                                <input
                                                    type="number"
                                                    placeholder="Unit Price"
                                                    value={item.unitPrice}
                                                    onChange={(e) => handleItemChange(index, 'unitPrice', e.target.value)}
                                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                                    step="0.01"
                                                    required
                                                />
                                            </div>

                                            <div className="col-span-1 font-medium">
                                                {formatCurrency(item.total)}
                                            </div>

                                            <div className="col-span-1">
                                                {formData.items.length > 1 && (
                                                    <button
                                                        type="button"
                                                        onClick={() => removeItem(index)}
                                                        className="p-2 text-red-600 hover:text-red-700 hover:bg-red-50 rounded-lg"
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </button>
                                                )}
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            {/* Tax and Discount */}
                            <div className="grid grid-cols-2 gap-6">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Tax Amount ($)
                                    </label>
                                    <input
                                        type="number"
                                        value={formData.tax}
                                        onChange={(e) => setFormData(prev => ({ ...prev, tax: parseFloat(e.target.value) || 0 }))}
                                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                        step="0.01"
                                    />
                                </div>

                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-2">
                                        Discount ($)
                                    </label>
                                    <input
                                        type="number"
                                        value={formData.discount}
                                        onChange={(e) => setFormData(prev => ({ ...prev, discount: parseFloat(e.target.value) || 0 }))}
                                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                        step="0.01"
                                    />
                                </div>
                            </div>

                            {/* Notes */}
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-2">
                                    Notes
                                </label>
                                <textarea
                                    value={formData.notes}
                                    onChange={(e) => setFormData(prev => ({ ...prev, notes: e.target.value }))}
                                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary/20 focus:border-primary"
                                    rows={3}
                                    placeholder="Additional notes or terms..."
                                />
                            </div>

                            {/* Summary */}
                            <div className="bg-gray-50 p-4 rounded-xl">
                                <div className="flex justify-between mb-2">
                                    <span className="text-gray-600">Subtotal:</span>
                                    <span className="font-medium">{formatCurrency(calculations.subtotal)}</span>
                                </div>
                                <div className="flex justify-between mb-2">
                                    <span className="text-gray-600">Tax:</span>
                                    <span className="font-medium">+{formatCurrency(formData.tax)}</span>
                                </div>
                                <div className="flex justify-between mb-2">
                                    <span className="text-gray-600">Discount:</span>
                                    <span className="font-medium text-red-600">-{formatCurrency(formData.discount)}</span>
                                </div>
                                <div className="border-t border-gray-300 pt-2 mt-2">
                                    <div className="flex justify-between">
                                        <span className="text-lg font-bold">Total:</span>
                                        <span className="text-2xl font-bold text-primary">
                                            {formatCurrency(calculations.total)}
                                        </span>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Form Actions */}
                        <div className="flex justify-end space-x-3 mt-8">
                            <Button
                                type="button"
                                variant="outline"
                                onClick={onClose}
                            >
                                Cancel
                            </Button>

                            <Button
                                type="submit"
                                variant="primary"
                            >
                                {initialData ? 'Update Invoice' : 'Create Invoice'}
                            </Button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}